body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.App {
}
.monitor {
    background-color: #282c34;
    min-height: 100vh;
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: column;
            flex-direction: column;
    -webkit-align-items: center;
            align-items: center;
    color: white;
}

.monitor__header {
    font-size: calc(10px + 2vmin);
}
.monitor__details {
    font-size: 16px;
}

.monitor__details span {
    padding: 4px;
}

.monitor__services {
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: row;
            flex-direction: row;
    -webkit-flex-wrap: wrap;
            flex-wrap: wrap;
    -webkit-justify-content: center;
            justify-content: center;
}
.service-tile {
    height: 200px;
    width: calc(30vmax + 100px);
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: column;
            flex-direction: column;
    -webkit-align-items: center;
            align-items: center;
    padding: 8px;
    margin: 20px;
    background: grey;
    color: white;
    border-radius: 4px;
}

.service-tile--healthy {
    background: #2e9a1d;
}

.service-tile--unhealthy {
    background: #c33434;
}

.service__name {
    font-size: 60px;
    margin-top: 40px;
    margin-bottom: 20px ;
}

